import styled from 'styled-components';

const Planp = styled.div`
   background:white;
   width:275px;


`;

function Planpackage(props) {


  return (
    <Planp >
      <header></header>
      <p></p>
      <div>
        <span><div><div>{props.pages[0]}</div><div>Pages</div></div></span>
        <span></span>
        <span></span>
      </div>

    </Planp>
  );
}

export default Planpackage;
