import styled from 'styled-components';
import {useState} from 'react';
import {useSelector,useDispatch} from 'react-redux';
import Pageheadingmain from'../components/Pageheadingmain.js';
import Pageheadingsub from'../components/Pageheadingsub.js';
import Planpackage from '../components/Planpackage.js';


const Plan=styled.div`

  display:grid;
  grid-template-rows:repeat(auto-fit,1fr);
  grid-row-gap:80px;
  >section:first-child {
    display:flex;
    flex-direction:column;
    align-items:center;
    justify-content:flex-start;
  }

  >section:nth-child(2) {
    max-width:1150px;
    justify-self:center;
    >div {


      display:flex;
      justify-content:center;
      flex-wrap:wrap;

      >div {
        width:230px;
        margin:10px;
        padding:10px;
        text-align:center;
        color:${props=>props.theme.black2};
        >header {
          font-size:20px;
          font-weight:bold;
        }
        >p {
          font-size:16px;
          margin:5px;
        }
      }


    }

    >header {
      color:${props=>props.theme.blue1};
      font-size:24px;
      font-weight:bold;
    }
  }


`;


function Plans () {




  return (
    <Plan>
      <section>
        <Pageheadingmain content={"Plans & Pricing"}/>
        <Pageheadingsub content={"Made for People/Businesses with a Straightforward Online Presence"}/>
        <Pageheadingsub content={"If You Have Specific Needs, Discuss the Solutions We Have Just for You!"}/>
      </section>
      <section>
        <header>All Plans Include</header>
        <div>
          <div><header>Something Else</header><p>Lorem ipsum dolor sit amet, consetetur sadipscing elitr, sed diam nonumy eirmod tempor invidunt ut labore et dolore magna aliquyam</p></div>
          <div><header>Continued Maintenance</header><p>Your website is tested on a monthly basis to make sure things are running smoothly. We won't leave you hanging.</p></div>
          <div><header>Fully Customized</header><p>We don't use pre-made templates to make your website. Everything is custom-tailored for your brand and image.</p></div>
          <div><header>Direct Contact</header><p>You don't have to jump through hoops to get in touch with someone who knows your particular situation.</p></div>
        </div>
      </section>


      <section>
        <div>
          <div className="headtitle">
            <span>1</span>
            <header>Choose the Best Size for your Website</header>
          </div>
          <div>
            <Planpackage pages={[1, 5]} hosting={30} price={65}/>

          </div>

        </div>



        <div></div>
        <div></div>
        <div></div>
      </section>

    </Plan>
  );

}


export default Plans;
